function sliderMain() {
    const container = document.querySelector('.main__slider');
    const swiper = new Swiper('.main__slider .mySwiper', {
        slidesPerView: 1,
        spaceBetween: 0,
        direction: 'vertical',
        effect: 'fade',
        mousewheel: true,
        simulateTouch: true,
        on: {
            reachEnd: () => {
                // Отключаем свайпы слайдера
                swiper.allowTouchMove = false;

                // Разрешаем прокрутку контейнера
                container.style.overflowY = 'auto';

                // Добавляем слушатели для переключения обратно
                container.addEventListener('touchstart', handleTouchStart, { passive: false });
                container.addEventListener('touchmove', handleTouchMove, { passive: false });
                container.addEventListener('touchend', handleTouchEnd);
            }
        }
    });

    // Изначально прокрутка контейнера заблокирована, свайпы включены
    container.style.overflowY = 'hidden';
    swiper.allowTouchMove = true;

    let touchStartY = 0;
    let touchEndY = 0;

    function handleTouchStart(e) {
        touchStartY = e.touches[0].clientY;
    }

    function handleTouchMove(e) {
        touchEndY = e.touches[0].clientY;
        const deltaY = touchStartY - touchEndY;

        // Если свайп вверх (прокрутка вниз страницы) — блокируем скролл страницы, чтобы слайдер не потерял управление
        if (deltaY > 0) {
            e.preventDefault();
        }
        // Если свайп вниз — не блокируем, даём странице прокрутиться
    }

    function handleTouchEnd(e) {
        const sliderRect = container.getBoundingClientRect();
        const deltaY = touchStartY - touchEndY;

        // Если слайдер виден и свайп вниз (пользователь хочет скроллить страницу вверх)
        if (sliderRect.top >= 0 && deltaY < 0) {
            // Включаем свайпы слайдера
            swiper.allowTouchMove = true;

            // Блокируем прокрутку контейнера
            container.style.overflowY = 'hidden';

            // Убираем слушатели, чтобы не мешали
            container.removeEventListener('touchstart', handleTouchStart);
            container.removeEventListener('touchmove', handleTouchMove);
            container.removeEventListener('touchend', handleTouchEnd);
        }
    }
}

$(document).ready(function () {
    sliderMain();

    // Menu
    $('.burger').click(function () {
        $('.menu').addClass('menu--active')
        $('body').addClass('fixed-body')
    })

    $('.menu__close').click(function () {
        $('.menu').removeClass('menu--active')
        $('body').removeClass('fixed-body')
    })

    // Btn-animation
    $(".btn-animation").mouseenter(function(e) {
        var $this = $(this);
        var $animation = $this.find(".circle");
        var offset = $this.offset();
        var x = e.pageX - offset.left;
        var y = e.pageY - offset.top;
        $animation.css({
            top: y + 'px',
            left: x + 'px'
        });
    });

    $(".btn-animation").mousemove(function(e) {
        var $this = $(this);
        var $animation = $this.find(".circle");
        var offset = $this.offset();
        var x = e.pageX - offset.left;
        var y = e.pageY - offset.top;
        $animation.css({
            top: y + 'px',
            left: x + 'px'
        });
    });

    // Form
    let isClicked = false;

    $('.form').on('submit', function(e) {
        e.preventDefault();

        isClicked = true;
        let allFilled = true;

        $(this).find('.form-group input').each(function() {
            const $input = $(this);
            const $formGroup = $input.closest('.form-group');

            if ($input.val().trim() === '') {
                $formGroup.addClass('form-group--active');
                allFilled = false;
            } else {
                $formGroup.removeClass('form-group--active');
            }
        });
    });

    $('.form-group input').on('input', function() {
        if (isClicked) {
            const $input = $(this);
            const $formGroup = $input.closest('.form-group');

            if ($input.val().trim() === '') {
                $formGroup.addClass('form-group--active');
            } else {
                $formGroup.removeClass('form-group--active');
            }
        }
    });

    // Animation
    gsap.registerPlugin(ScrollTrigger);

    // Main
    function getAnimationParams() {
        const isMobile = window.matchMedia('(max-width: 1200px)').matches;
        if (isMobile) {
            return {
                mainItemsY: -250,
                mainItemsScale: 1.2,
            };
        } else {
            return {
                mainItemsY: -350,
                mainItemsScale: 1.6,
            };
        }
    }

    function createTimeline() {
        if (window.matchMedia('(max-width: 992px)').matches) {
            if (window.myTimeline) {
                window.myTimeline.kill();
                window.myTimeline = null;
            }
            return;
        }

        const params = getAnimationParams();

        if (window.myTimeline) {
            window.myTimeline.kill();
        }

        window.myTimeline = gsap.timeline({
            scrollTrigger: {
                trigger: ".main",
                start: "top top",
                end: "bottom top",
                scrub: true,
                pin: true,
                pinSpacing: false,
            }
        })
            .to(".main__title", {
                opacity: 0,
                ease: "power1.out",
            }, 0)
            .to(".main__btn", {
                y: -100,
                opacity: 0,
                scale: 0.8,
                ease: "power1.inOut",
            }, 0)
            .to(".main__items", {
                y: params.mainItemsY,
                scale: params.mainItemsScale,
                ease: "power1.inOut",
            }, 0);
    }

    createTimeline();

    window.addEventListener('resize', () => {
        createTimeline();
    });

    if (!window.matchMedia('(max-width: 992px)').matches) {
        var $main = $('.main');
        var originalBg = $main.data('background');

        $main.css('background-image', 'url(' + originalBg + ')');

        $('.main__item').on('mouseenter', function() {
            var newBg = $(this).data('background');
            $main.css('background-image', 'url(' + newBg + ')');
        });

        $('.main__item').on('mouseleave', function() {
            $main.css('background-image', 'url(' + originalBg + ')');
        });
    }

    // Main-mob


})

